import uniPopup from "../popup.vue"
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			isLogin: false,
			minute_show: false,
			serve_show: false
		}
	},
	methods: {
		jumpAgreement() {
			uni.navigateTo({
				url: '../agreement/agreement',
			});
		},
		Login() {
			this.isLogin = !this.isLogin
		},
		closeServe(val) {
			this.serve_show = !this.serve_show
		},
		closeMain(val) {
			this.minute_show = !this.minute_show
		},
		jumpWallet(val) {
			uni.navigateTo({
				url: 'wallet',
			});
		},
		jumpOrder(val) {
			uni.navigateTo({
				url: '../sell_order/sell_order',
			});
		},
		jumpDetail(val) {
			if (val == "yh") {
				uni.navigateTo({
					url: 'coupon'
				})
			} else if (val == 'up') {
				uni.navigateTo({
					url: '../vip/up_vip'
				})
			} else if (val == 'zc') {
				uni.navigateTo({
					url: '../vip/policy_vip'
				})
			}else if(val=='centre'){
				uni.navigateTo({
					url: 'my_centre'
				})
			}
		},
		jumpTeam(val) {
			uni.navigateTo({
				url: 'my_team',
			});
		}
	}
}
